			getMsgdianhua: function(v) {

				// 子组件传值，给父组件，修改父组件DATA


				//匹配移动手机号
				var PATTERN_CHINAMOBILE = /^1(3[4-9]|5[012789]|8[23478]|4[7]|7[8])\d{8}$/
				// 匹配联通手机号 
				var PATTERN_CHINAUNICOM = /^1(3[0-2]|5[56]|8[56]|4[5]|7[6])\d{8}$/;
				// 匹配电信手机号 
				var PATTERN_CHINATELECOM = /^((133)|(149)|(153)|(173)|(191)|(18[0|1|9])|(177))[\d]{8}$/;



				if (v.length >= 3) {
					var telphone3 = v.substr(0, 3);
				}
				if (v.length >= 4) {
					var telphone4 = v.substr(0, 4);
				}
				if (
					telphone3 == 170 ||
					telphone3 == 165 ||
					telphone3 == 167 ||
					telphone3 == 171 ||
					telphone4 == 170 ||
					telphone4 == 1700 ||
					telphone4 == 1702 ||
					telphone4 == 1703 ||
					telphone4 == 1705 ||
					telphone4 == 1706 ||
					telphone4 == 1707 ||
					telphone4 == 1708 ||
					telphone4 == 1709

				) {
					this.phoneType = ""
					uni.showToast({
						title: "暂不支持此号码充值",
						icon: "none"
					})
					return;
				}

				if (PATTERN_CHINATELECOM.test(v) === true) {
					this.phoneType = "中国电信"
					this.productAlias="CTCC"
					console.log("6")
				} else
				if (PATTERN_CHINAUNICOM.test(v) === true) {
					console.log("7")
					this.phoneType = "中国联通"
					this.productAlias="CUCC"
				} else
				if (PATTERN_CHINAMOBILE.test(v) === true) {
					console.log("8")
					this.phoneType = "中国移动"
					this.productAlias="CMCC"
				}
				if (v.length == 11 &&
					this.phoneType != "中国电信" &&
					this.phoneType != "中国联通" &&
					this.phoneType != "中国移动") {
					uni.showToast({
						icon: "none",
						title: "手机号码有误"
					})
					return
				}




			},


			alilogin() {
				let _self = this;

				// 0. 显示加载的效果
				uni.showLoading({
					title: '登录中...'
				});
				// 1. wx 获取登录用户 code
				uni.login({
					provider: 'alipay',
					success: loginRes => {
						console.log(loginRes);
						_self.code = loginRes.code;
						// 2. 将用户登录code传递到后台置换用户SessionKey、OpenId等信息
						uni.request({
							url: 'https://ccc.wangwanglive.com/index.php/api/Alixcx/api_alilogin?code=' +
								_self.code +
								'&grant_type=authorization_code',
							success: codeRes => {
								console.log(codeRes.data);
								_self.id = codeRes.data.memberid;

								// 3.通过 openId 判断用户是否授权

								// 隐藏loading
								uni.hideLoading();


								// 用户信息写入缓存
								uni.showToast({
									title: '登录成功'
								})
								let userlist = {
									user_id: codeRes.data.memberid,
									user_nm: codeRes.data.wx_name,
									user_img: codeRes.data.img_url

								}
								uni.setStorageSync('userlist', userlist);
								// 然后跳回原页面



							},
